import { styleHeaderBanner } from "./Elements/HeaderConstant";
import { GET_MEDIA_NAME } from "../../../utils";
import { CloudHostUrl, TagboxUpload } from "../../../constants";
import { ensureFullyQualifiedUrl } from "../../../utils";
const HeaderType5 = ({ Banner }) => {
  const { bannerStyle, subTitleStyle, bannerStyleBgRight, titleStyle, iconStyle, isFacebook, isTwitter, isInstagram, facebookIconStyle, twitterIconStyle, instagramIconStyle, subTitleVarentClass, titleVarentClass } = styleHeaderBanner(Banner);

  var bLogoImg = Banner.LogoImage;
  if (bLogoImg && String(bLogoImg).length > 0 && String(bLogoImg).includes("banner/logo_bnr") && String(bLogoImg).includes(TagboxUpload)) bLogoImg = String(Banner.LogoImage).replace(TagboxUpload, "")
  else if (bLogoImg.includes(TagboxUpload) && bLogoImg.split(TagboxUpload)[1] === '') bLogoImg = String(Banner.LogoImage).replace(TagboxUpload, "")
  else if (bLogoImg && String(bLogoImg).length > 0) {
    let LogoImage = bLogoImg.split('/').pop();
    if (LogoImage == 'logo_bnr-2.svg' || LogoImage == 'logo_bnr-3.svg' || LogoImage == 'logo_bnr-5.svg') {
      bLogoImg = `${CloudHostUrl}react-app/media/banner/logo_bnr-5.svg`
    }
  }
  return <div className="tb_wall_header_wrap__" style={bannerStyle}>
    <div className="tb_header5_row__">
      <div className="tb_header5_column_logo__">
        {bLogoImg ?
          <div className="tb_header5_logo__">
            <img loading="lazy" className="tb_header5_logo_img__" src={bLogoImg} alt={GET_MEDIA_NAME(bLogoImg)} width={180} height={52} />
          </div>
          : ''}
      </div>
      <div className="tb_header5_column_content__">
        <div className="tb_header5_content__">
          <div className={`tb_header5_subtitle__ tb-cTBfont-${subTitleVarentClass}`} style={subTitleStyle}>{Banner.subTitle}</div>
          <div className={`tb_header5_title__ tb-cTBfont-${titleVarentClass}`} style={titleStyle}>{Banner.title}</div>
        </div>
      </div>
      <div className="tb_header5_column_icons__">
        {Banner.social_icon_status == 1 ? <div className="tb_header5_right_bg" style={bannerStyleBgRight}> </div> : ''}
        {Banner.social_icon_status == 1 ? <div className="tb_header5_social_icons__">
          {isFacebook ?
            <a href={Banner.facebook_url ? ensureFullyQualifiedUrl(Banner.facebook_url) : null} target="_blank" className="tb_header5_social_icon_list__" aria-label="Facebook">
              <div className={`tb_header5_social_icon__ tb__icon tb-facebook`} style={{ ...iconStyle, ...facebookIconStyle }}> </div>
            </a> : ''}
          {isTwitter ?
            <a href={Banner.twitter_url ? ensureFullyQualifiedUrl(Banner.twitter_url) : null} target="_blank" className="tb_header5_social_icon_list__" aria-label="Twitter">
              <div className="tb_header5_social_icon__ tb__icon tb-twitter" style={{ ...iconStyle, ...twitterIconStyle }}></div>
            </a> : ''}
          {isInstagram ?
            <a href={Banner.instagram_url ? ensureFullyQualifiedUrl(Banner.instagram_url) : null} target="_blank" className="tb_header5_social_icon_list__" aria-label="Instagram">
              <div className="tb_header5_social_icon__ tb__icon tb-instagram" style={{ ...iconStyle, ...instagramIconStyle }}> </div>
            </a> : ''}
        </div>
          : null} </div>
    </div>
  </div>
}

export default HeaderType5
